.App{
  min-height: 98vh;
  background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
  text-align: center;
  padding: 20px;
}
.searchBar, .submitButton{
  vertical-align: middle;
}
nav{
  position: relative;
}

.searchBar{
  margin: 10px auto;
  width: 35%;
  border: none;
  border-radius: 8px;
  height: 30px;
  text-align: center;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.submitButton{
  margin: 10px auto;
  width: 50px;
  background: black;
  color: white;
  height: 32px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}
.submit{
  color: white;
}
.find{
  width: 80%;
  height: 100%;
}
.word-container{
  margin: 0 50%;
  margin-top: 70px;
  width: 40%;
  height: auto;
  text-align: left;
 
  transform: translate(-50%,0%);

}
.defi-container{
  margin: 10px;
  width: 80%;
  min-height: auto;
  padding: 20px;
  background-color: white;
  border-radius: 20px;
}
.searched-word{
  font-size: 3rem;
  font-weight: bold;
  color: #134FE6;
  margin-left: 10px;
  text-transform: capitalize;
}
.type{
  font-weight: 500;
  text-transform: capitalize;
}
.pronunciation{
  margin-left: 15px;
} 
.definition{
  margin-top: 10px;
}
.example{
  font-style: italic;
}


/* bouncing balls */
.bouncer {
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  width: 100px;
  height: 100px;
  margin: 20px auto;
}
.bouncer div {
  width: 20px;
  height: 20px;
  background: #0077ff;
  border-radius: 50%;
  animation: bouncer 0.5s cubic-bezier(.19,.57,.3,.98) infinite alternate;
}
.bouncer div:nth-child(2) {
  animation-delay: 0.1s;
  opacity: 0.8;
}
.bouncer div:nth-child(3) {
  animation-delay: 0.2s;
  opacity: 0.6;
}
.bouncer div:nth-child(4) {
  animation-delay: 0.3s;
  opacity: 0.4;
}

@keyframes bouncer {
  from { transform: translateY(0) }
  to { transform: translateY(-100px) }
}

